@import '~assets/styles/h3Imports';

.additional-menu {
  border-bottom: 1px solid var(--menu-item-separator);
  display: none;
  width: 100%;

  [data-element='text-button'] {
    svg {
      pointer-events: none;
    }
  }
}

@media #{$large-down} {
  .additional-menu {
    display: inline-block;
    padding: 16px 0;
    padding-left: 24px;

    button {
      display: inline-flex;
      justify-content: flex-start;
      margin-left: rem(8);
      min-width: 0;
      overflow: hidden;
      padding: 0;
      width: auto;

      svg {
        padding: rem(8) rem(6);
      }
    }

    [data-popup-menu='popup-menu-item-1'],
    [data-popup-menu='popup-menu-item-2'] {
      display: block;
      margin-top: var(--spacing-xs);

      .text {
        @include paragraph;

        color: var(--gray-5);
        display: inline-flex;
      }

      ul {
        padding-left: 0;
        padding-right: 0;
        z-index: 12;

        li {
          padding-left: 0;
          padding-right: 0;

          &[element-type='A'] button :last-child {
            font-size: rem(16);
          }

          &[element-type='AA'] button :last-child {
            font-size: rem(19);
          }

          &[element-type='AAA'] button :last-child {
            font-size: rem(27);
          }

          &[element-type='bold'] button :last-child {
            @include interactive-text;

            font-weight: 400;
          }

          &[data-class='selected'] button {
            background-color: var(--interface-info-background);
          }

          &[data-class='selected'][element-type='bold'] button :last-child {
            font-weight: 600;
          }
        }

        :first-child button {
          border-radius: 8px 8px 0 0;
        }

        :last-child button {
          border-radius: 0 0 8px 8px;
        }

        button {
          align-items: center;
          border-radius: 0;
          display: inline-flex;
          height: rem(56);
          margin: 0;
          max-height: 100%;
          max-width: 100%;
          padding: 0 rem(16);
          width: 100%;

          :first-child {
            margin-left: auto;
          }

          :first-child svg {
            height: rem(28);
            width: rem(28);
          }

          :last-child {
            @include h2;

            color: var(--gray-5);
          }
        }
      }

      &:first-child {
        margin: 0;
      }
    }
  }
}
